@import "styles/variables";

.container {
  padding: 10px 5px 10px 50px;

  p {
    margin-left: 10px;
    font-family: $font-family-title;
    color: $primary-color;

    font-size: 17px;
    line-height: 19px;
  }

  margin-top: 14px;
}

.inner {
  position: relative;
}

.arrowImg {
  width: 15px;
  height: 10px;
  margin-left: 8px;
  transition: all 0.4s linear;
}
.arrowImgSecond {
  margin-top: 3px;
  width: 13px;
  height: 8px;
  margin-left: 8px;
  transition: all 0.4s linear;
}
.arrowImgActive {
  transform: rotate(-180deg);
}

.subMenuContainer {
  height: 0;
  overflow: hidden;

  transition: all 0.4s linear;
}

.subMenuContainerActive {
  height: 100%;
}

.subMenuItem {
  padding-left: 30px;

  padding-top: 14px;

  position: relative;

  span {
    font-family: $font-family-title;
    color: $primary-color;

    font-size: 17px;
    line-height: 19px;
  }
  .soon {
    font-family: $font-family-title;
    color: $primary-color;

    font-size: 12px;
    line-height: 19px;
  }
}
